import { gql } from '@apollo/client';

export const QUERY_USER = gql`
  query user($username: String!) {
    user(username: $username) {
      _id
      username
      email
      parlays {
        _id
        parlay_name
        createdAt
      }
    }
  }
`;

export const QUERY_PARLAYS = gql`
  query getParlays {
    parlays {
      _id
      parlay_name
      createdAt
    }
  }
`;

export const QUERY_GAMES = gql`
  query getGames {
    games {
      _id
      home_team
      home_price
      home_point
      away_team
      away_price
      away_point
      commence_time
    }
  }
`;

export const QUERY_SINGLE_PARLAY = gql`
  query getSingleParlay($parlayId: ID!) {
    parlay(parlayId: $parlayId) {
      _id
      parlay_name
      createdAt
      outcomes {
        _id
        team_choice
        odds
        spread
        commence_time
      }
    }
  }
`;

export const QUERY_ME = gql`
  query me {
    me {
      _id
      username
      email
      parlays {
        _id
        parlay_name
        createdAt
      }
    }
  }
`;