import React from 'react';
import { useParams } from 'react-router-dom';
import { useQuery } from '@apollo/client';

import OutcomeList from '../components/OutcomeList';
import OutcomeForm from '../components/OutcomeForm';

import { QUERY_SINGLE_PARLAY } from '../utils/queries';

const SingleParlay = () => {
  const { parlayId } = useParams();

  const { loading, data } = useQuery(QUERY_SINGLE_PARLAY, {
    // pass URL parameter
    variables: { parlayId: parlayId },
  });

  const parlay = data?.parlay || {};

  if (loading) {
    return <div>Loading...ğŸ’°...Create Your Next...ğŸ’¸...Winning Parlay Ticket...ğŸ«</div>;
  }
  return (
    <div className="my-3">
      <h3 className="card-header bg-dark text-light p-2 m-0">
        {parlay.username} <br />
        <span style={{ fontSize: '1rem' }}>
          had this ticket as of ğŸ«: {parlay.createdAt}
        </span>
      </h3>
      <div className="bg-light py-4">
        <blockquote
          className="p-4"
          style={{
            fontSize: '1.5rem',
            fontStyle: 'italic',
            border: '2px solid black',
            lineHeight: '1.5',
          }}
        >
          {parlay.parlay_name}
        </blockquote>
      </div>

      <div className="my-5">
        <OutcomeList outcomes={parlay.outcomes} />
      </div>
      <div className="m-3 p-4" style={{ border: '1px solid black' }}>
        <OutcomeForm parlayId={parlay._id} />
      </div>
    </div>
  );
};

export default SingleParlay;