import React, { useState, useEffect } from 'react';
import GameList from '../components/GameList';
import ParlayList from '../components/ParlayList';
import ParlayForm from '../components/ParlayForm';

import { QUERY_PARLAYS } from '../utils/queries';

var date = (new Date()).toString().split(' ').splice(1,3).join(' ');
document.write(date);

const Home = () => {
  const [parlays, setData] = useState([])
  const [isLoading, setLoading] = useState(false)

  useEffect(() => {
    setLoading(true)
    fetch(QUERY_PARLAYS)
      .then((res) => res.json())
      .then((parlays) => {
        setData(parlays)
        setLoading(false)
      })
  }, [])

  if (isLoading) return <p>ğŸ’¸...Which Picks Look Like A Lock ğŸ”’â”...ğŸ’¸</p>
  if (!parlays) return <p>Errorâ—â›”...No Parlays Available...â—â›”</p>

  return (
    <main>
      {parlays.map(parlay => (
      <div className="flex-row justify-center" key={parlay._id}>
        <div className="flex-row justify-center" data="zoom-in">
          <h5 id="list-title">Scheduled ğŸˆ Games: {date} ğŸ“‹</h5>
        <GameList />
        </div>
        <div data="slide-right"
          className="col-12 col-md-10 mb-3 p-3"
          style={{ border: '1px solid black' }}
        >
          <ParlayForm />
        </div>
        <div className="col-12 col-md-8 mb-3" data="slide-left">
          {isLoading ? (
            <div>ğŸ’¸...Which Picks Look Like A Lock ğŸ”’â”...ğŸ’¸</div>
          ) : (
            <ParlayList
              parlays={parlays}
              title="Pick The Next Winner...ğŸ’°...Winner...ğŸ’°...Chicken...ğŸ”...Dinner!"
            />
          )}
        </div>
      </div>
      ))}
    </main>
  )
};

export default Home;