import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useMutation } from '@apollo/client';

import { ADD_PARLAY } from '../utils/mutations';
import { QUERY_PARLAYS, QUERY_ME } from '../utils/queries';

import Auth from '../utils/auth';

const ParlayForm = () => {
  const [parlay_name, setParlayName] = useState('');

  const [characterCount, setCharacterCount] = useState(0);

  const [addParlay, { error }] = useMutation(ADD_PARLAY, {
    update(cache, { data: { addParlay } }) {
      try {
        const { parlays } = cache.readQuery({ query: QUERY_PARLAYS });

        cache.writeQuery({
          query: QUERY_PARLAYS,
          data: { parlays: [addParlay, ...parlays] },
        });
      } catch (e) {
        console.error(e);
      }

      // update me object's cache
      const { me } = cache.readQuery({ query: QUERY_ME });
      cache.writeQuery({
        query: QUERY_ME,
        data: { me: { ...me, parlays: [...me.parlays, addParlay] } },
      });
    },
  });

  const handleFormSubmit = async (event) => {
    event.preventDefault();

    try {
      const { data } = await addParlay({
        variables: {
          parlay_name,
          username: Auth.getProfile().data.username,
        },
      });

      setParlayName('');
    } catch (err) {
      console.error(err);
    }
  };

  const handleChange = (event) => {
    const { name, value } = event.target;

    if (name === 'parlay_name' && value.length <= 280) {
      setParlayName(value);
      setCharacterCount(value.length);
    }
  };

  return (
    <div>
      <h3>ğŸ’°...Create Your Next...ğŸ’¸...Winning Parlay Ticket...ğŸ«</h3>

      {Auth.loggedIn() ? (
        <>
          <p
            className={`m-0 ${
              characterCount === 280 || error ? 'text-danger' : ''
            }`}
          >
            Character Count: {characterCount}/280
          </p>
          <form data="slide-right"
            className="flex-row justify-center justify-space-between-md align-center"
            onSubmit={handleFormSubmit}
          >
            <div className="col-12 col-lg-9">
              <textarea
                name="parlay_name"
                placeholder="ğŸ’°...Winner...Winner...Chicken...ğŸ”...Dinner...ğŸ’°"
                value={parlay_name}
                className="form-input w-100"
                style={{ lineHeight: '1.5', resize: 'vertical' }}
                onChange={handleChange}
              ></textarea>
            </div>

            <div className="col-12 col-lg-3">
              <button className="btn btn-primary btn-block py-3" type="submit">
                Create Ticket ğŸ“¬
              </button>
            </div>
            {error && (
              <div className="col-12 my-3 bg-danger text-white p-3">
                {error.message}
              </div>
            )}
          </form>
        </>
      ) : (
        <p>
          â›” You need to be logged in to create a ticketâ— Pleaseâ—{' '}
          <Link to="/login">Login ğŸ“¡</Link> or <Link to="/signup">Sign Up ğŸ”</Link>
        </p>
      )}
    </div>
  );
};

export default ParlayForm;