import { gql } from '@apollo/client';

export const LOGIN_USER = gql`
  mutation login($email: String!, $password: String!) {
    login(email: $email, password: $password) {
      token
      user {
        _id
        username
      }
    }
  }
`;

export const ADD_USER = gql`
  mutation addUser($username: String!, $email: String!, $password: String!) {
    addUser(username: $username, email: $email, password: $password) {
      token
      user {
        _id
        username
      }
    }
  }
`;

export const ADD_PARLAY = gql`
  mutation addParlay($parlay_name: String!) {
    addParlay(parlay_name: $parlay_name) {
      _id
      parlay_name
      createdAt
      outcomes {
        _id
      team_choice
      odds
      spread
      commence_time
      }
    }
  }
`;

export const ADD_OUTCOME = gql`
  mutation addOutcome($parlayId: ID!, $team_choice: String!, $odds: Int!, $spread: Int!) {
    addOutcome(parlayId: $parlayId, team_choice: $team_choice, odds: $odds, spread: $spread) {
      _id
      parlay_name
      createdAt
      outcomes {
        _id
        team_choice
        odds
        spread
        commence_time
      }
    }
  }
`;