import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { useMutation } from '@apollo/client';

import { ADD_OUTCOME } from '../utils/mutations';

import Auth from '../utils/auth';
import { check } from 'prettier';

const OutcomeForm = ({ parlayId }) => {

  const [team_choice, setTeamChoice] = useState(false);
  const [odds, setOdds] = useState(false);
  const [spread, setSpread] = useState(false);
  const [valueCount, setValueCount] = useState(false);

  const [addOutcome, { error }] = useMutation(ADD_OUTCOME);

  const handleFormSubmit = async (event) => {
    event.preventDefault();

    try {
      const { data } = await addOutcome({
        variables: {
          parlayId,
          team_choice,
          odds,
          spread,
          username: Auth.getProfile().data.username,
        },
      });

      setTeamChoice(false);
      setOdds(false);
      setSpread(false);
    } catch (err) {
      console.error(err);
    }
  };

  const handleChange = (event) => {
    const { checkbox, value } = event.target;

    if (checkbox === check && value <= true) {
      setTeamChoice(value);
      setOdds(value);
      setSpread(value);
      setValueCount(true);
    }
  };

  return (
    <div>
      <h4>ğŸ’¸...Which Picks Look Like A Lock ğŸ”’â”...ğŸ’¸</h4>

      {Auth.loggedIn() ? (
        <>
          <p
            className={`m-0 ${
              valueCount === true || error ? 'text-danger' : ''
            }`}
          >
            Parlay Selections Confirmed âœ…: {valueCount}
            {error && <span className="ml-2">{error.message}</span>}
          </p>
          <form
            className="flex-row justify-center justify-space-between-md align-center"
            onSubmit={handleFormSubmit}
          >
            <div className="col-12 col-lg-9" data="slide-right">
              <input
                checkbox={check}
                value={team_choice}
                className="form-input w-100"
                style={{ lineHeight: '1.5', resize: 'vertical' }}
                onChange={handleChange}
              ></input>
              <label for={team_choice}>{team_choice}</label>
            </div>
            <div className="col-12 col-lg-9" data="slide-left">
              <input
                checkbox={check}
                value={odds}
                className="form-input w-100"
                style={{ lineHeight: '1.5', resize: 'vertical' }}
                onChange={handleChange}
              ></input>
               <label for={odds}>{odds}</label>
            </div>
            <div className="col-12 col-lg-9" data="slide-right">
              <input
                checkbox={check}
                value={spread}
                className="form-input w-100"
                style={{ lineHeight: '1.5', resize: 'vertical' }}
                onChange={handleChange}
              ></input>
               <label for={spread}>{spread}</label>
            </div>

            <div className="col-12 col-lg-3">
              <button className="btn btn-primary btn-block py-3" type="submit">
                Add Selections ğŸ’¸
              </button>
            </div>
          </form>
        </>
      ) : (
        <p>
           â›” You need to be logged in to make picksâ— Pleaseâ—{' '}
          <Link to="/login">Login ğŸ“¡</Link> or <Link to="/signup">Sign Up ğŸ”</Link>
        </p>
      )}
    </div>
  );
};

export default OutcomeForm;